import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import '../../models/event';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class EventsService {
  constructor(private http: HttpClient) {}

  events = new BehaviorSubject<[Event]>(null);

  getAll() {
    return this.http.get(baseUrl + '/event').subscribe((res) => {
      if (res['data']) {
        this.events.next(res['data']);
      }
    });
  }

  getOne(itemId) {
    return this.http.get(baseUrl + `/event/${itemId}`);
  }

  addOne(form) {
    return this.http.post(baseUrl + '/event', form);
  }

  deleteOne(itemId) {
    return this.http.delete(baseUrl + `/event/${itemId}`);
  }
}
